// pehelp.defaultValueExpression: "This setting allows you to assign a default answer value based on an expression. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression serves as the initial default value that can be overridden by a respondent's manual input." => "Essa configurao permite atribuir um valor de resposta padro com base em uma expresso. A expresso pode incluir clculos bsicos - '{q1_id} + {q2_id}', expresses booleanas, como '{age} > 60', e funes: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()', etc. O valor determinado por essa expresso serve como o valor padro inicial que pode ser substitudo pela entrada manual de um respondente."
// pehelp.resetValueIf: "Use the magic wand icon to set a conditional rule that determines when a respondent's input is reset to the value based on the \"Default value expression\" or \"Set value expression\" or to the \"Default answer\" value (if either is set)." => "Use o cone de varinha mgica para definir uma regra condicional que determina quando a entrada de um respondente  redefinida para o valor com base no valor \"Expresso de valor padro\" ou \"Definir expresso de valor\" ou no valor \"Resposta padro\" (se um dos dois estiver definido)."
// pehelp.setValueIf: "Use the magic wand icon to set a conditional rule that determines when to run the \"Set value expression\" and dynamically assign the resulting value as a response." => "Use o cone de varinha mgica para definir uma regra condicional que determine quando executar a expresso \"Definir valor\" e atribuir dinamicamente o valor resultante como resposta."
// pehelp.setValueExpression: "Specify an expression that defines the value to be set when the conditions in the \"Set value if\" rule are met. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression can be overridden by a respondent's manual input." => "Especifique uma expresso que defina o valor a ser definido quando as condies na regra \"Definir valor se\" forem atendidas. A expresso pode incluir clculos bsicos - '{q1_id} + {q2_id}', expresses booleanas, como '{age} > 60', e funes: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()', etc. O valor determinado por essa expresso pode ser substitudo pela entrada manual de um respondente."
