// pehelp.choicesFromQuestionMode: "Choose from: \"All\" - copies all choice options from the selected question; \"Selected\" - dynamically copies only selected choice options; \"Unselected\" - dynamically copies only unselected choice options. The \"None\" and \"Other\" options are copied by default if enabled in the source question." => "Escolha entre: \"Todos\" - copia todas as opes de escolha da pergunta selecionada; \"Selecionado\" - copia dinamicamente apenas as opes de escolha selecionadas; \"No selecionado\" - copia dinamicamente apenas as opes de escolha no selecionadas. As opes \"Nenhum\" e \"Outros\" so copiadas por padro se ativadas na pergunta de origem."
